import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import Web3 from "web3";
import lottery_abi from "../abis/lottery_abi.json";
import {Button, Container, Grid, List, ListItem, ListItemText, Paper, Typography} from "@mui/material";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat} from "../utils/currency_format";
import CoineusLoading from "../components/CoineusLoading";

export default function LotteryGame(props) {

    const {lottery_contract_address, rpc, currency} = props;

    const {wallet} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);

    const [amount, setAmount] = useState(0);
    const [organizer, setOrganizer] = useState(null);
    const [players, setPlayers] = useState([]);
    const [lotteryState, setLotteryState] = useState(1);
    const [ticketCost, setTicketCost] = useState(0);
    const [adminFee, setAdminFee] = useState(0);
    const [lotteryID, setLotteryID] = useState(0);
    const [previousWinner, setPreviousWinner] = useState('');
    const [stats, setStats] = useState({wins: 0, entries: 0, winnings: 0});
    const [totalWinnings, setTotalWinnings] = useState(0);

    const getLotteryInfo = () => {

        if (!wallet.address) return;


        const web3 = new Web3(rpc);
        const contract = new web3.eth.Contract(lottery_abi, lottery_contract_address);

        const calls = [
            contract.methods.lottery_state().call,
            contract.methods.getPlayers().call,
            contract.methods.organizer().call,
            contract.methods.ticket_cost().call,
            contract.methods.admin_fee().call,
            contract.methods.lotteryID().call,
            web3.eth.getBalance(lottery_contract_address),
            contract.methods.getStatsByAddress(wallet.address).call,
            contract.methods.totalWinnings().call
        ];

        const divider = currency === "FUSE" ? 1 : (10 ** 18);

        makeBatchRequest(web3, calls).then(resp => {
            setLotteryState(parseInt(resp[0]));
            setPlayers(resp[1]);
            setOrganizer(resp[2]);
            setTicketCost(resp[3] / divider);
            setAdminFee(resp[4]);
            setLotteryID(resp[5]);
            setAmount(resp[6] / (10 ** 18));
            setStats(resp[7]);
            setTotalWinnings(resp[8] / (10 ** 18));

            contract.methods.getWinnerByLotteryID(resp[5] - 1).call((err, resp) => {
                setPreviousWinner(resp);
            });
        })
    }

    useEffect(() => {
        getLotteryInfo();
        const interval = setInterval(getLotteryInfo, 10000)
        return () => clearInterval(interval);
    }, [rpc, wallet.address]);

    const pickWinnner = () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(lottery_abi, lottery_contract_address);

        contract.methods.pickWinner("coineus" + (new Date()).getTime())
            .send(
                {
                    from: wallet.address
                }
            )
            .then(function (receipt) {
                getLotteryInfo()
            });

    }

    const openLottery = () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(lottery_abi, lottery_contract_address);

        contract.methods.open_lottery()
            .send(
                {
                    from: wallet.address
                }
            )
            .then(function (receipt) {
                getLotteryInfo()
            });

    }

    const enter = async () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(lottery_abi, lottery_contract_address);
        const gasPrice = await web3.eth.getGasPrice();

        contract.methods.enter()
            .send(
                {
                    from: wallet.address,
                    value: web3.utils.toWei(ticketCost.toString()),
                    gas: 300000,
                    gasPrice
                }
            )
            .once('transactionHash', function (tx) {
                setPending(true);
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                getLotteryInfo()
                setPending(false);
            });
    }

    const checkChain = () => {
        switch (currency) {
            case "AVAX":
                if (wallet.chain === 43114) return true;
                break;
            case "BNB":
                if (wallet.chain === 56) return true;
                break;
            case "FUSE":
                if (wallet.chain === 122) return true;
                break;
            default:
                return false;
        }
    }

    const changeNetwork = async () => {
        let chainID = 122;
        switch (currency) {
            case "AVAX":
                chainID = 43114;
                break;
            case "BNB":
                chainID = 56;
                break;
            case "FUSE":
                chainID = 122;
                break;
            default:
                return false;
        }

        const web3 = new Web3(window.provider);
        await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{chainId: web3.utils.toHex(chainID)}], // chainId must be in hexadecimal numbers
        });
    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                {
                    wallet.address === organizer && <Grid item xs={12}>
                        <Button variant="contained" onClick={pickWinnner} style={{marginRight: 10}}>Pick Winner</Button>
                        <Button variant="contained" onClick={openLottery} disabled={lotteryState === 0}>Open
                            Lottery</Button>
                    </Grid>
                }

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #fbca00'}}>
                        <List>
                            <ListItem>
                                <ListItemText primary={`${CoineusCryptoFormat(totalWinnings)} ${currency}`}
                                              primaryTypographyProps={{
                                                  style: {fontWeight: 700},
                                                  variant: 'h5',
                                                  color: 'primary',
                                                  textAlign: 'center'
                                              }}
                                              secondary="Total Winnings All-time"
                                              secondaryTypographyProps={{textAlign: 'center'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <Typography variant="caption" color="primary" style={{
                            float: "right",
                            padding: 10,
                            marginBottom: -30
                        }}>Draw# {lotteryID}</Typography>
                        <List>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(amount - (amount / adminFee))} ${currency}`}
                                    primaryTypographyProps={{
                                        style: {fontWeight: 700},
                                        variant: 'h5',
                                        color: 'primary'
                                    }}
                                    secondary="Current Jackpot"/>
                            </ListItem>
                            <ListItem>
                                {
                                    checkChain()
                                        ?
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            disabled={!checkChain() || lotteryState === 1 || players.indexOf(wallet.address) > -1}
                                            onClick={enter}
                                        >{lotteryState === 1 ? "Lottery Closed" : (players.indexOf(wallet.address) > -1 ? "Already Entered" : `BUY TICKET (${ticketCost} ${currency})`)}</Button>
                                        :
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            onClick={changeNetwork}>CHANGE NETWORK</Button>
                                }


                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <ListItem>
                                <ListItemText
                                    primary={`0X...${previousWinner?.winner?.substr(-6)}`}
                                    secondary="Previous Winner"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(previousWinner?.amount / (10 ** 18))} ${currency}`}
                                    primaryTypographyProps={{textAlign: 'right'}}
                                    secondary="Amount"
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <ListItem>
                                <ListItemText
                                    primary={stats.entries}
                                    secondary="My Entries"
                                />
                                <ListItemText
                                    primary={stats.wins}
                                    secondary="My Wins"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(stats.winnings)} ${currency}`}
                                    secondary="My Winnings"
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{padding: 15}}>
                        <List dense disablePadding>
                            {
                                players.length > 0 && <>
                                    <Typography>{players.length} Entries</Typography>
                                    {
                                        players.map(player => {
                                            return <ListItem key={player} disablePadding>
                                                <ListItemText
                                                    primary={player}
                                                    primaryTypographyProps={{
                                                        color: player.toLowerCase() === wallet.address.toLowerCase() ? "primary" : "",
                                                        variant: "caption",
                                                        fontFamily: 'monospace'
                                                    }}
                                                />
                                            </ListItem>
                                        })
                                    }
                                </>
                            }

                            {
                                players.length <= 0 && <ListItem>
                                    <ListItemText primary="No Entries"/>
                                </ListItem>
                            }

                        </List>
                    </Paper>
                </Grid>
            </Grid>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Container>
    );
}