import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {Link as RouterLink} from "react-router-dom";
import Web3 from "web3";
import lottery_abi from "../abis/lottery_abi.json";
import {
    Avatar,
    Button,
    Container,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Typography
} from "@mui/material";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat} from "../utils/currency_format";

const LOTTERY_CONTRACT1 = "0x8cCd3537AEee2D66447F01DafEA1ca427C13e3e6";
const LOTTERY_CONTRACT2 = "0x8B03aBEA721C79bd426EC2561eCB88542Db3fca7";
const LOTTERY_CONTRACT3 = "0x3A1E450e2a49C3c88Aefd0b0Db3Ac13301dc635F";

const LOTTERY_CONTRACT_BNB = "0x79cAD46Dc5b8B3355F8A2Ff80192471Ae096a65B";
const LOTTERY_CONTRACT_AVAX = "0xEcFd74Bf46f6DDE3b88109f80AEEb8020de06A58";

export default function Lottery() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [lottery1, setLottery1] = useState({});
    const [lottery2, setLottery2] = useState({});
    const [lottery3, setLottery3] = useState({});

    const [lotteryBNB, setLotteryBNB] = useState({});

    const [lotteryAVAX, setLotteryAVAX] = useState({});

    const getLotteryInfo = () => {

        if (!wallet.address) return;

        const web3 = new Web3(rpc.fuse);

        const lottery1 = new web3.eth.Contract(lottery_abi, LOTTERY_CONTRACT1);
        const lottery2 = new web3.eth.Contract(lottery_abi, LOTTERY_CONTRACT2);
        const lottery3 = new web3.eth.Contract(lottery_abi, LOTTERY_CONTRACT3);

        const calls = [

            lottery1.methods.ticket_cost().call,
            lottery1.methods.admin_fee().call,
            lottery1.methods.lotteryID().call,
            web3.eth.getBalance(LOTTERY_CONTRACT1),

            lottery2.methods.ticket_cost().call,
            lottery2.methods.admin_fee().call,
            lottery2.methods.lotteryID().call,
            web3.eth.getBalance(LOTTERY_CONTRACT2),

            lottery3.methods.ticket_cost().call,
            lottery3.methods.admin_fee().call,
            lottery3.methods.lotteryID().call,
            web3.eth.getBalance(LOTTERY_CONTRACT3),

        ];

        makeBatchRequest(web3, calls).then(resp => {

            setLottery1({
                ticketCost: resp[0],
                adminFee: resp[1],
                lotteryID: resp[2],
                jackpot: (resp[3] / (10 ** 18)) * (1 - resp[1]/100)
            })

            setLottery2({
                ticketCost: resp[4],
                adminFee: resp[5],
                lotteryID: resp[6],
                jackpot: (resp[7] / (10 ** 18)) * (1 - resp[5]/100)
            })

            setLottery3({
                ticketCost: resp[8],
                adminFee: resp[9],
                lotteryID: resp[10],
                jackpot: (resp[11] / (10 ** 18)) * (1 - resp[9]/100)
            })

        })

        const web3BNB = new Web3(rpc.bnb);
        const lotteryBNB = new web3BNB.eth.Contract(lottery_abi, LOTTERY_CONTRACT_BNB);

        const callsBNB = [
            lotteryBNB.methods.ticket_cost().call,
            lotteryBNB.methods.admin_fee().call,
            lotteryBNB.methods.lotteryID().call,
            web3BNB.eth.getBalance(LOTTERY_CONTRACT_BNB)
        ];

        makeBatchRequest(web3BNB, callsBNB).then(resp => {
            setLotteryBNB({
                ticketCost: resp[0] / (10**18),
                adminFee: resp[1],
                lotteryID: resp[2],
                jackpot: (resp[3] / (10 ** 18)) * (1 - resp[1]/100)
            })
        });

        const web3AVAX = new Web3(rpc.avax);
        const lotteryAVAX = new web3AVAX.eth.Contract(lottery_abi, LOTTERY_CONTRACT_AVAX);

        const callsAVAX = [
            lotteryAVAX.methods.ticket_cost().call,
            lotteryAVAX.methods.admin_fee().call,
            lotteryAVAX.methods.lotteryID().call,
            web3AVAX.eth.getBalance(LOTTERY_CONTRACT_AVAX)
        ];

        makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
            setLotteryAVAX({
                ticketCost: resp[0] / (10**18),
                adminFee: resp[1],
                lotteryID: resp[2],
                jackpot: (resp[3] / (10 ** 18)) * (1 - resp[1]/100)
            })
        });
    }

    useEffect(() => {
        getLotteryInfo();
        const interval = setInterval(getLotteryInfo, 10000)
        return () => clearInterval(interval);
    }, [rpc.fuse, wallet.address]);

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <LotteryGameInfo
                    index={1}
                    id={lottery1.lotteryID}
                    cost={lottery1.ticketCost}
                    jackpot={lottery1.jackpot}
                    currency="FUSE"
                />

                <LotteryGameInfo
                    index={2}
                    id={lottery2.lotteryID}
                    cost={lottery2.ticketCost}
                    jackpot={lottery2.jackpot}
                    currency="FUSE"
                />

                <LotteryGameInfo
                    index={3}
                    id={lottery3.lotteryID}
                    cost={lottery3.ticketCost}
                    jackpot={lottery3.jackpot}
                    currency="FUSE"
                />

                <LotteryGameInfo
                    index={"bnb"}
                    id={lotteryBNB.lotteryID}
                    cost={lotteryBNB.ticketCost}
                    jackpot={lotteryBNB.jackpot}
                    currency="BNB"
                />

                <LotteryGameInfo
                    index={"avax"}
                    id={lotteryAVAX.lotteryID}
                    cost={lotteryAVAX.ticketCost}
                    jackpot={lotteryAVAX.jackpot}
                    currency="AVAX"
                />

            </Grid>
        </Container>
    );
}

function LotteryGameInfo(props) {

    const {wallet} = useSelector(state => state.coineus);

    const {index, id, cost, jackpot, currency} = props;

    const checkChain = () => {
        switch (currency) {
            case "AVAX":
                if (wallet.chain === 43114) return true;
                break;
            case "BNB":
                if (wallet.chain === 56) return true;
                break;
            case "FUSE":
                if (wallet.chain === 122) return true;
                break;
            default:
                return false;
        }
    }

    const changeNetwork = async () => {
        let chainID = 122;
        switch (currency) {
            case "AVAX":
                chainID = 43114;
                break;
            case "BNB":
                chainID = 56;
                break;
            case "FUSE":
                chainID = 122;
                break;
            default:
                return false;
        }

        const web3 = new Web3(window.provider);
        await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{chainId: web3.utils.toHex(chainID)}], // chainId must be in hexadecimal numbers
        });
    }

    return <Grid item xs={12}>
        <Paper>
            <Typography variant="caption" color="primary" style={{float: "right", padding: 10}}>Draw# {id}</Typography>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar src={`https://coineus.app/assets/tokens/${currency.toLowerCase()}/logo.png`} />
                    </ListItemAvatar>
                    <ListItemText primary={`${CoineusCryptoFormat(cost)} ${currency}`}
                                  primaryTypographyProps={{
                                      style: {fontWeight: 700},
                                      variant: 'h5',
                                      color: 'primary'
                                  }}
                                  secondary="Ticket Price"/>
                    <ListItemText primary={`${CoineusCryptoFormat(jackpot)} ${currency}`}
                                  primaryTypographyProps={{
                                      style: {fontWeight: 700},
                                      variant: 'h5',
                                      color: 'primary',
                                      textAlign: 'right'
                                  }}
                                  secondary="Current Jackpot"
                                  secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                <ListItem>

                    {
                        checkChain()
                            ?
                            <RouterLink to={`/lottery/${index}`} style={{width: '100%', textDecoration: 'none'}}>
                                <Button
                                    fullWidth
                                    variant="contained"
                                >Enter</Button>
                            </RouterLink>
                            :
                            <Button
                                fullWidth
                                variant="contained"
                                onClick={changeNetwork}>CHANGE NETWORK</Button>
                    }
                </ListItem>
            </List>
        </Paper>
    </Grid>
}