import LotteryGame from "./LotteryGame";
import {useSelector} from "react-redux";

export default function LotteryBNB() {

    const {rpc} = useSelector(state => state.coineus);

    return (
        <LotteryGame
            lottery_contract_address="0x79cAD46Dc5b8B3355F8A2Ff80192471Ae096a65B"
            rpc={rpc.bnb}
            currency="BNB"
        />
    );
}